.intro {
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-family: "Montserrat";
}

.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: -1;
}
.intro::after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: rgba(0, 0, 0, 0.404);
  position: absolute;
}
.introInner {
  z-index: 1;
  display: flex;
}
.container {
  width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}
.introContent {
  width: 700px;
  color: rgb(250, 250, 250);
  font-size: 30px;
  display: flex;
  flex-direction: column;
  align-items: start;
}
.introContent span span {
  color: rgb(26, 187, 179);
}
.introContent button {
  margin: 0;
}
